<template>
  <div id="app">
      <div class="damage" :key="dmg.name" v-for="dmg in $store.state.damage">
          <div>
            <b-progress :value="dmg.damage" :max="$store.state.selected.oppostats[$store.state.selected.oppolevel - 1]['hp']" show-progress animated></b-progress>
          </div>
          <div>{{dmg.name}} 데미지: {{dmg.damage}}</div>
          <div v-if="dmg.tick > 1">
            <b-progress :value="dmg.damage * dmg.tick" :max="$store.state.selected.oppostats[$store.state.selected.oppolevel - 1]['hp']" show-progress animated></b-progress>
          </div>
          <div v-if="dmg.tick > 1">틱({{dmg.tick}}번)당 데미지 총합 : {{dmg.damage * dmg.tick}}</div>
      </div>
      <div class="sum" v-if="$store.state.damage.length > 1">
        <div>
            <b-progress :value="$store.state.maxdamage" :max="$store.state.selected.oppostats[$store.state.selected.oppolevel - 1]['hp']" show-progress animated></b-progress>
          </div>
          <div>총 데미지: {{$store.state.maxdamage}}</div>
      </div>
      <div class="err" v-if="$store.state.damage.length === 0">현재 정보로 데미지를 계산할 수 없습니다.</div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        sum: 0
      }
    },
    methods: {
      getSum() {
        this.sum = 0;
        for (let dmg of this.$store.state.damage) {
          this.sum += dmg.damage;
        }
        return this.sum;
      }
    }
  }
</script>

<style>
.damage {
  margin-bottom: 1rem;
}
</style>