import axios from 'axios'

export const GetPokemonList = () => {
    axios.defaults.headers.post['Access-Control-Allow-Origin'] = '*';
    return axios.get('https://iant.kr:5500/pokemonList')
}

export const GetPokemonInfo = (index) => {
    axios.defaults.headers.post['Access-Control-Allow-Origin'] = '*';
    return axios.get(`https://iant.kr:5500/pokemon/${index}`)
}