import { GetPokemonList, GetPokemonInfo } from '../api/index'

export const actions = {
    GET_PKLIST(ctx) {
        GetPokemonList()
            .then(res => {
                ctx.commit("updatePokemonList", res.data);
            }).catch(err => {
                console.log(err);
            });
    },
    GET_PKINFO(ctx, payload) {
        GetPokemonInfo(payload[0])
            .then(res => {
                ctx.commit('updatePokemonInfo', [res.data, payload[1]]);
            }).catch(err => {
                console.log(err);
            });
    },
    CAL_DAMAGE(ctx) {
        let usingSkill = ctx.state.skill[ctx.state.selected.myskill];
        if (usingSkill.canCalculate === true) {
            let returnList = [];

            if (usingSkill.damage.isMulti === true) {
                for (let usingBonusSkill of usingSkill.bonusDamage) {
                    let dmgName = usingBonusSkill.name;
                    let calculatedDmg = calculateDamage(usingBonusSkill, ctx.state.selected.mylevel, ctx.state.selected.mystats, ctx.state.selected.oppolevel, ctx.state.selected.oppostats);
                    returnList.push({
                        "name": dmgName,
                        "damage": Math.floor(calculatedDmg),
                        "tick": usingBonusSkill.tick
                    });
                }
            }
            else {
                let calculatedDmg = calculateDamage(usingSkill, ctx.state.selected.mylevel, ctx.state.selected.mystats, ctx.state.selected.oppolevel, ctx.state.selected.oppostats);
                returnList.push({
                    "name": "메인",
                    "damage": Math.floor(calculatedDmg),
                    "tick": usingSkill.tick
                });
                if (usingSkill.bonusDamage.length > 0) {
                    for (let usingBonusSkill of usingSkill.bonusDamage) {
                        let dmgName = usingBonusSkill.name;
                        let calculatedDmg = calculateDamage(usingBonusSkill, ctx.state.selected.mylevel, ctx.state.selected.mystats, ctx.state.selected.oppolevel, ctx.state.selected.oppostats);
                        returnList.push({
                            "name": dmgName,
                            "damage": Math.floor(calculatedDmg),
                            "tick": usingBonusSkill.tick
                        });
                    }
                }
            }

            ctx.commit('setDmg', returnList);
        } else {
            ctx.commit('setDmg', []);
        }
    }
}

const calculateDamage = (usingSkill, myLevel, myStats, oppoLevel, oppoStats) => {
    if (usingSkill.damage.isHPRatio) {
        return Math.floor(usingSkill.damage.HPRatio * oppoStats[oppoLevel - 1].hp);
    }

    let calculatedDmg = Math.floor(usingSkill.damage.attackRatio * myStats[myLevel - 1][usingSkill.damage.using]) + Math.floor(usingSkill.damage.levelRatio * (myLevel - 1)) + usingSkill.damage.bonus;
    if (oppoStats.length > 1) {
        if (usingSkill.damage.using === "attack") {
            calculatedDmg = calculatedDmg * 600 / (600 + oppoStats[oppoLevel - 1]['defense']);
        } else {
            calculatedDmg = calculatedDmg * 600 / (600 + oppoStats[oppoLevel - 1]['sp_defense']);
        }
    }

    return calculatedDmg;
}